<template lang="pug">
main
  div.wrapper
    Photo(v-for='photo in photos' :photo='photo' :key='photo.id')
  div.fixed
     span(@click.stop="setPage(1)") 1
     span ...
     span(@click.stop="setPage(currentPage-1)") {{currentPage-1}}
     span#selected(@click.stop="setPage(currentPage)") {{currentPage}}
     span(@click.stop="setPage(currentPage+1)") {{currentPage+1}}
     span ...
     span(@click.stop="setPage(59)") 59

</template>

<script>
import Photo from '@/components/Photo.vue'
export default {
  name: 'Work',
  data () {
    return {
      currentPage: 6
    }
  },
  mounted () {
    this.$store.dispatch('getPhotos', this.currentPage)
  },
  methods: {
    setPage (page) {
      this.$store.dispatch('getPhotos', page)
      if (page > 2 && page < 58) {
        this.currentPage = page
      }
    }
  },
  computed: {
    photos () {
      return this.$store.getters.getPhotos
    }
  },
  components: {
    Photo
  }
}
</script>

<style lang="scss" scoped>
 main {
       width: 1000px;
       height: max-content;
       margin: auto;
  }
  .wrapper {
     width: 660px;
     margin: auto;
     display: grid;
     grid-template-columns: repeat(2, 320px);
     grid-column-gap: 20px;
     padding-bottom: 60px;
  }
  .fixed {
    position: fixed;
    background: #000000;
    opacity: 0.9;
    width: 100%;
    height: 60px;
    bottom: 0px;
    left: 0px;
    color:white;
    display: grid;
    grid-auto-flow: column;
    grid-gap: 15px;
    align-items: center;
    justify-content: center;
    #selected {
      font-size: 18px;
      font-weight: bold;
    }
}
 @media screen and (max-width: 768px){
   main {
     width: 768px;
   }
 }
 @media screen and (max-width: 480px){
   main {
     width: 480px;
   }
   .wrapper {
     width: 480px;
     grid-template-columns: repeat(1, 440px);
     padding-left: 20px;
  }
 }
 @media screen and (max-width:320px){
      main {
     width: 320px;
   }
 }
</style>
