import Vue from 'vue'
import VueRouter from 'vue-router'
import Workle from '../views/Workle.vue'
import Work from '../views/Work.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/work',
    name: 'Workle',
    component: Workle
  },
  {
    path: '/',
    name: 'Work',
    component: Work
  }
]

const router = new VueRouter({
  routes
})

export default router
