import unsplashAPI from '../../api/unsplash.js'
import Vue from 'vue'
export default {
  state: {
    photos: [],
    stats: {},
    loading: false
  },
  mutations: {
    SET_PHOTOS (state, payload) {
      state.photos = payload
    },
    SET_STATS (state, payload) {
      Vue.set(state.stats, payload.id, payload)
    },
    SET_LOADING (state, payload) {
      state.loading = payload
    }
  },
  actions: {
    async getPhotos ({ commit }, page) {
      try {
        commit('SET_LOADING', true)
        const response = await unsplashAPI.get('photos', {
          params: {
            page
          }
        })
        commit('SET_PHOTOS', response.data)
        commit('SET_LOADING', false)
      } catch (e) {
        console.log(e)
      }
    },
    async getStat ({ commit }, id) {
      try {
        const response = await unsplashAPI.get(`photos/${id}/statistics`)
        commit('SET_STATS', response.data)
      } catch (e) {
        console.log(e)
      }
    }
  },
  getters: {
    getPhotos: state => state.photos,
    getStat: state => id => state.stats[id],
    getLoadingState: state => state.loading
  }
}
